import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './HorizonAngle.css';

export default class HorizonAngle extends Component {
    static propTypes = {
        adi: PropTypes.number,
    }

    render() {
        return (
            <div className="Horizon-Container">
                <div style={{
                    backgroundImage: `linear-gradient(blue ${this.props.adi <= 0 ? 0 : this.props.adi}%, #00ff00 ${this.props.adi >= 0 ? 100 : parseInt(this.props.adi) + 100}%)`,
                    width: "25vw", height: "25vw",
                    borderRadius: "50%",
                    border: "solid white",
                    borderWidth: "0.25vw",
                    margin: "0 auto",
                    position: "relative",
                }}>

                </div>
            </div>
        )
    }
}
